/* 기본 리셋 & 폰트 */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
html,
body,
#root {
  height: 100%;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    "Noto Sans KR", Arial, "Apple SD Gothic Neo", "맑은 고딕", sans-serif;
  color: #1f2937; /* slate-800 */
  background: #f8fafc; /* slate-50 */
  line-height: 1.6;
}

/* 앱 컨테이너 */
.App {
  max-width: 760px;
  margin: 0 auto;
  padding: 24px 20px 80px;
}

/* 상단 네비 */
.black-nav {
  position: sticky;
  top: 0;
  z-index: 10;
  background: #0f172a; /* slate-950 */
  color: #e2e8f0; /* slate-200 */
  padding: 16px 20px;
  border-radius: 14px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
  box-shadow: 0 10px 30px rgba(2, 6, 23, 0.35);
}
.black-nav h4 {
  font-size: 18px;
  letter-spacing: 0.2px;
}

/* 게시글 카드 리스트 */
.list {
  background: #ffffff;
  border: 1px solid #e5e7eb; /* gray-200 */
  border-radius: 14px;
  padding: 18px 16px;
  margin: 14px 0;
  box-shadow: 0 8px 20px rgba(15, 23, 42, 0.06);
  transition: transform 0.12s ease, box-shadow 0.12s ease,
    border-color 0.12s ease;
}
.list:hover {
  transform: translateY(-2px);
  box-shadow: 0 12px 28px rgba(15, 23, 42, 0.1);
  border-color: #d1d5db; /* gray-300 */
}

/* 게시글 타이틀 */
.list h4 {
  font-size: 18px;
  font-weight: 700;
  color: #0f172a; /* slate-950 */
  cursor: pointer;
  margin-bottom: 8px;
  display: inline-block;
}
.list h4:hover {
  text-decoration: underline;
}

/* 좋아요/카운트 */
.list span {
  user-select: none;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  gap: 6px;
  padding: 6px 10px;
  margin-right: 8px;
  border-radius: 999px;
  border: 1px solid #e5e7eb;
  background: #f8fafc;
  transition: background 0.15s ease, border-color 0.15s ease;
}
.list span:hover {
  background: #eef2ff; /* indigo-50 */
  border-color: #c7d2fe; /* indigo-200 */
}
.list p {
  margin-top: 8px;
  font-size: 13px;
  color: #6b7280; /* gray-500 */
}

/* 버튼 공통 */
button {
  cursor: pointer;
  border: 1px solid #d1d5db;
  background: #111827;
  color: #ffffff;
  padding: 8px 12px;
  border-radius: 10px;
  font-size: 14px;
  transition: transform 0.1s ease, box-shadow 0.12s ease, background 0.12s ease,
    color 0.12s ease, border-color 0.12s ease;
}
button:hover {
  transform: translateY(-1px);
  box-shadow: 0 8px 18px rgba(17, 24, 39, 0.18);
}
button:active {
  transform: translateY(0);
}

/* 파괴적 버튼(삭제) */
.list button {
  margin-left: 10px;
  background: #dc2626; /* red-600 */
  border-color: #dc2626;
}
.list button:hover {
  background: #b91c1c; /* red-700 */
  border-color: #b91c1c;
}

/* 입력창 + 발행 버튼 줄 */
.App input {
  width: 70%;
  max-width: 520px;
  padding: 10px 12px;
  border-radius: 10px;
  border: 1px solid #d1d5db;
  background: #ffffff;
  outline: none;
  margin-top: 12px;
  margin-right: 8px;
  font-size: 14px;
  transition: box-shadow 0.12s ease, border-color 0.12s ease,
    background 0.12s ease;
}
.App input:focus {
  border-color: #818cf8; /* indigo-400 */
  box-shadow: 0 0 0 4px rgba(99, 102, 241, 0.15);
}
.App > button {
  background: #4f46e5; /* indigo-600 */
  border-color: #4f46e5;
}
.App > button:hover {
  background: #4338ca; /* indigo-700 */
  border-color: #4338ca;
}

/* 모달(상세) */
.modal {
  position: relative;
  margin-top: 18px;
  background: #ffffff;
  border: 1px solid #e5e7eb;
  border-radius: 14px;
  padding: 18px 16px 16px;
  box-shadow: 0 20px 40px rgba(2, 6, 23, 0.12);
  animation: modal-pop 0.16s ease-out;
}
@keyframes modal-pop {
  from {
    transform: scale(0.98);
    opacity: 0;
  }
  to {
    transform: scale(1);
    opacity: 1;
  }
}
.modal h4 {
  font-size: 18px;
  font-weight: 800;
  margin-bottom: 6px;
}
.modal p {
  font-size: 14px;
  color: #4b5563; /* gray-600 */
  margin: 2px 0 10px;
}
.modal button {
  background: #0ea5e9; /* sky-500 */
  border-color: #0ea5e9;
}
.modal button:hover {
  background: #0284c7; /* sky-600 */
  border-color: #0284c7;
}

/* 반응형 */
@media (max-width: 520px) {
  .App {
    padding: 18px 14px 60px;
  }
  .black-nav {
    border-radius: 12px;
  }
  .list {
    padding: 16px 14px;
  }
  .App input {
    width: 100%;
    margin: 12px 0 10px;
  }
  .App > button {
    width: 100%;
  }
}
